import React, { useState } from "react";
import ReactECharts from "echarts-for-react";
import { Select } from "antd";
import dayjs from "dayjs";

const BarLineChart = ({
  data = { xAxis: [], price: [], quantity: [] },
  onDateFormatChange,
}) => {
  const [dataZoom, setDataZoom] = useState(10);
  const [dateFormat, setDateFormat] = useState(4);
  const options = {
    tooltip: {
      trigger: "axis",
      axisPointer: {
        type: "cross",
        label: {
          backgroundColor: "#283b56",
        },
      },
    },
    grid: {
      left: "3%",
      right: "4%",
      containLabel: true,
    },
    legend: {
      data: ["", "", "", ""],
    },
    // backgroundColor: "black",
    xAxis: [
      {
        type: "category",
        boundaryGap: true,
        data: data.xAxis,
        axisLabel: {
          formatter: (value, index) => {
            const day = dayjs(value).format("HH:mm:ss");
            return day;
          },
        },
      },
    ],
    yAxis: [
      {
        type: "value",
        scale: true,
        name: "",
      },
      {
        type: "value",
        scale: true,
        name: "",
      },
    ],
    dataZoom: [
      {
        show: true,
        realtime: true,
        startValue: dataZoom ? data.xAxis.length - dataZoom : 0,
        endValue: data.xAxis.length - 1,
        onChange: (val) => {
          console.log(val);
        },
      },
    ],
    series: [
      {
        name: "",
        type: "line",
        step: "end",
        data: data.price,
        itemStyle: {
          color: "black",
        },
      },
      {
        name: "",
        type: "line",
        step: "end",
        data: data.buy,
        itemStyle: {
          color: "red",
        },
      },
      {
        name: "",
        type: "line",
        step: "end",
        data: data.sell,
        itemStyle: {
          color: "green",
        },
      },
      {
        name: "",
        type: "bar",
        yAxisIndex: 1,
        data: data.quantity,
        itemStyle: {
          color: "yellow",
        },
      },
    ],
  };

  return (
    <>
      <div className="flex flex-row">
        <div className="flex flex-col justify-end p-4">
          
          <Select
            className="w-40"
            value={dataZoom}
            options={[
              { label: "10", value: 10 },
              { label: "50", value: 50 },
              { label: "100", value: 100 },
              { label: "", value: 0 },
            ]}
            onChange={(val) => setDataZoom(val)}
          />
        </div>
        <div className="flex flex-col justify-end p-4">
          
          <Select
            className="w-40"
            value={dateFormat}
            options={[
              { label: "", value: 4 },
              { label: "", value: 3 },
              { label: "", value: 0 },
              { label: "", value: 1 },
              { label: "", value: 2 },
            ]}
            onChange={(val) => {
              onDateFormatChange && onDateFormatChange(val);
              setDateFormat(val);
            }}
          />
        </div>
      </div>
      <ReactECharts option={options} />
    </>
  );
};

export default BarLineChart;
